.about-us {
  @include page-grid;

  &__title {
    grid-column: 1/-1;

    @include on-tablet {
      grid-column: 1/4;
    }

    @include on-desktop {
      grid-column: span 5;
    }
  }

  &__description {
    grid-column: 1/-1;

    margin: 0;
    line-height: 32.2px;
    font-size: 23px;
    font-weight: 500;

    @include on-tablet {
      grid-column: 4/7;
    }

    @include on-desktop {
      grid-column: span 7;
    }
  }
}
